// contexts/AuthContext.tsx
'use client'

import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react'
import { useRouter } from 'next/navigation'

interface User {
  id: number
  username: string
  email: string
  first_name: string
  last_name: string
}

interface Profile {
  id: number
  role: 'VISITOR' | 'MEMBER' | 'DEACON' | 'ELDER' | 'PASTOR' | 'ADMIN' | 'WEBMASTER'
  phone?: string
  is_active: boolean
}

interface AuthContextType {
  user: User | null
  profile: Profile | null
  token: string | null
  isAuthenticated: boolean
  isLoading: boolean
  isAdmin: boolean
  isWebmaster: boolean
  canManageContent: boolean
  canManageUsers: boolean
  canManageEverything: boolean
  canManageMembers: boolean
  canManageEvents: boolean
  canManageArticles: boolean
  canManageMedia: boolean
  canManageSettings: boolean
  canViewAdminPanel: boolean
  hasRole: (roles: string[]) => boolean
  login: (username: string, password: string) => Promise<void>
  register: (data: RegisterData) => Promise<void>
  logout: () => void
  refreshUser: () => Promise<void>
  updateProfile: (profileData: Partial<Profile>) => void
}

interface RegisterData {
  username: string
  password: string
  email: string
  first_name: string
  last_name: string
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export const useAuth = () => {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider')
  }
  return context
}

// Fonction utilitaire pour gérer le localStorage en toute sécurité
const safeLocalStorage = {
  getItem(key: string): string | null {
    if (typeof window === 'undefined') return null
    try {
      return localStorage.getItem(key)
    } catch (error) {
      console.error(`LocalStorage error (get ${key}):`, error)
      return null
    }
  },

  setItem(key: string, value: string): void {
    if (typeof window === 'undefined') return
    try {
      localStorage.setItem(key, value)
    } catch (error) {
      console.error(`LocalStorage error (set ${key}):`, error)
    }
  },

  removeItem(key: string): void {
    if (typeof window === 'undefined') return
    try {
      localStorage.removeItem(key)
    } catch (error) {
      console.error(`LocalStorage error (remove ${key}):`, error)
    }
  },

  getJSON<T>(key: string): T | null {
    const item = this.getItem(key)
    if (!item || item === 'undefined') return null
    try {
      return JSON.parse(item) as T
    } catch (error) {
      console.error(`LocalStorage error (parse ${key}):`, error)
      this.removeItem(key)
      return null
    }
  },

  setJSON(key: string, value: any): void {
    this.setItem(key, JSON.stringify(value))
  }
}

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<User | null>(null)
  const [profile, setProfile] = useState<Profile | null>(null)
  const [token, setToken] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const router = useRouter()

  useEffect(() => {
    // Vérifier l'authentification au chargement de manière sécurisée
    const storedToken = safeLocalStorage.getItem('access_token')
    const storedUser = safeLocalStorage.getJSON<User>('user')
    const storedMember = safeLocalStorage.getJSON<Profile>('member')
    const storedProfile = safeLocalStorage.getJSON<Profile>('profile')
    
    if (storedToken && storedUser) {
      setToken(storedToken)
      setUser(storedUser)
      
      // Priorité : member > profile (pour compatibilité avec votre API)
      if (storedMember) {
        setProfile(storedMember)
        safeLocalStorage.setJSON('profile', storedMember) // Synchroniser pour compatibilité
      } else if (storedProfile) {
        setProfile(storedProfile)
      }
    }
    setIsLoading(false)
  }, [])

  // Fonctions d'helper pour les permissions
  const isAuthenticated = !!user && !!token
  const isAdmin = profile?.role === 'ADMIN' || profile?.role === 'WEBMASTER'
  const isWebmaster = profile?.role === 'WEBMASTER'
  
  // Permissions spécifiques
  const canManageContent = isAdmin || profile?.role === 'PASTOR'
  const canManageUsers = isWebmaster
  const canManageEverything = isWebmaster
  const canManageMembers = isAdmin || profile?.role === 'PASTOR' || profile?.role === 'ELDER'
  const canManageEvents = isAdmin || profile?.role === 'PASTOR' || profile?.role === 'DEACON'
  const canManageArticles = isAdmin || profile?.role === 'PASTOR'
  const canManageMedia = isAdmin || profile?.role === 'PASTOR'
  const canManageSettings = isWebmaster
  const canViewAdminPanel = isAdmin || profile?.role === 'PASTOR'

  // Fonction utilitaire pour vérifier plusieurs rôles
  const hasRole = (roles: string[]): boolean => {
    if (!profile?.role) return false
    return roles.includes(profile.role)
  }

  const login = async (username: string, password: string) => {
    try {
      setIsLoading(true)
      const response = await fetch('http://localhost:8000/api/members/auth/login/', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ username, password }),
      })

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}))
        throw new Error(errorData.error || 'Identifiants invalides')
      }

      const data = await response.json()
      
      // Stocker les données en toute sécurité
      safeLocalStorage.setItem('access_token', data.access)
      safeLocalStorage.setItem('refresh_token', data.refresh)
      safeLocalStorage.setJSON('user', data.user)
      
      // Votre API backend renvoie 'member'
      if (data.member) {
        safeLocalStorage.setJSON('member', data.member)
        safeLocalStorage.setJSON('profile', data.member) // Doublon pour compatibilité
        setProfile(data.member)
      }
      
      setToken(data.access)
      setUser(data.user)
      
      router.push('/')
    } catch (error) {
      console.error('Login error:', error)
      throw error
    } finally {
      setIsLoading(false)
    }
  }

  const register = async (data: RegisterData) => {
    try {
      setIsLoading(true)
      const response = await fetch('http://localhost:8000/api/members/auth/register/', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
      })

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}))
        throw new Error(errorData.error || 'Erreur lors de l\'inscription')
      }

      const result = await response.json()
      
      // Stocker les données en toute sécurité
      safeLocalStorage.setItem('access_token', result.access)
      safeLocalStorage.setItem('refresh_token', result.refresh)
      safeLocalStorage.setJSON('user', result.user)
      
      // Votre API backend renvoie 'member'
      if (result.member) {
        safeLocalStorage.setJSON('member', result.member)
        safeLocalStorage.setJSON('profile', result.member) // Doublon pour compatibilité
        setProfile(result.member)
      }
      
      setToken(result.access)
      setUser(result.user)
      
      router.push('/')
    } catch (error) {
      console.error('Register error:', error)
      throw error
    } finally {
      setIsLoading(false)
    }
  }

  const logout = () => {
    // Nettoyer le localStorage de manière sécurisée
    safeLocalStorage.removeItem('access_token')
    safeLocalStorage.removeItem('refresh_token')
    safeLocalStorage.removeItem('user')
    safeLocalStorage.removeItem('member')
    safeLocalStorage.removeItem('profile') // Pour compatibilité
    
    setToken(null)
    setUser(null)
    setProfile(null)
    
    router.push('/')
  }

  const refreshUser = async () => {
    if (!token) return
    
    try {
      const response = await fetch('http://localhost:8000/api/members/auth/me/', {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
      })

      if (response.ok) {
        const data = await response.json()
        safeLocalStorage.setJSON('user', data.user)
        if (data.member) {
          safeLocalStorage.setJSON('member', data.member)
          safeLocalStorage.setJSON('profile', data.member) // Doublon pour compatibilité
          setProfile(data.member)
        }
        setUser(data.user)
      } else if (response.status === 401) {
        // Token invalide, déconnecter
        logout()
      }
    } catch (error) {
      console.error('Refresh user error:', error)
    }
  }

  const updateProfile = (profileData: Partial<Profile>) => {
    if (profile) {
      const updatedProfile = { ...profile, ...profileData }
      setProfile(updatedProfile)
      safeLocalStorage.setJSON('member', updatedProfile)
      safeLocalStorage.setJSON('profile', updatedProfile)
    }
  }

  return (
    <AuthContext.Provider value={{
      user,
      profile,
      token,
      isAuthenticated,
      isLoading,
      isAdmin,
      isWebmaster,
      canManageContent,
      canManageUsers,
      canManageEverything,
      canManageMembers,
      canManageEvents,
      canManageArticles,
      canManageMedia,
      canManageSettings,
      canViewAdminPanel,
      hasRole,
      login,
      register,
      logout,
      refreshUser,
      updateProfile,
    }}>
      {children}
    </AuthContext.Provider>
  )
}